.search-block{
  border: 1px solid #f3f3f3;
  border-radius: 10px;
  padding: 0 15px;
  position: relative;

  .clear{
    position: absolute;
    top: 13px;
    right: 15px;
    width: 18px;
    height: 18px;
  }

  input{
    border: none;
    padding: 12px;
    font-size: 16px;
    width: 200px;
  }
}

.overlay{
  position: absolute;
  left: 0;
  top: 0;
  background: rgba(0,0,0,0.1);
  z-index: 1;
  width: 100%;
  height: 100%;
}

.drawer{
  position: absolute;
  width: 420px;
  right: 0;
  height: 100%;
  background: #FFFFFF;
  box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.1);
  padding: 30px;
  display: flex;
  flex-direction: column;

  .items{
    flex: 1;
    overflow: auto;
    margin-bottom: 40px;
  }

  h2{
    margin: 0;
  }
}

.cartTotalBlock{
  ul{
    margin-bottom: 40px !important;
    li{
      display: flex;
      align-items: flex-end;
      margin-bottom: 20px;
    }

    div{
      flex:1;
      height: 1px;
      border-bottom: 1px dashed #dfdfdf;
      position: relative;
      top: -4px;
      margin: 0 10px;
    }
  }
}

.greenButton{
  position: relative;
    width: 100%;
    height: 55px;
    background: #9DD558;
    border-radius: 18px;
    border: none;
    color:#FFFFFF;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    transition: background 0.1s ease-in-out;
  &:disabled{
    animation: button-loading 0.6s ease-in-out infinite;
    background: #bebebe !important;
    cursor: default !important;
  }

  img{
    position: absolute;
    right: 30px;
    top: 20px;
    transition: transform 0.2s ease-in-out;
  }

    &:hover{
      background: lighten(#9dd558, 5%);

      & > img{
          transform: translateX(5px);
      }
    }

    &:active{
      background: darken(#9dd558, 5%);
    }
}

.cartItem{
  border: 1px solid #f3f3f3;
  border-radius: 20px;
  overflow: hidden;
  padding: 20px;

  .cartItemImg{
    height: 70px;
    width: 70px;
    background-size: contain;
    background-position: 0 -3px;
    background-repeat: no-repeat;
    margin-right: 20px;
  }
  p{
    font-size: 14px;
    margin: 0;
  }

  b{
    font-size: 16px;
  }

  .removeBtn{
    opacity: 0.5;
    cursor: pointer;
    transition: opacity 0.2s ease-in-out;

    &:hover{
      opacity: 1;
    }
  }
}

@keyframes button-loading {
  0%{
    opacity: 1;
  }
  50%{
  opacity: 0.7;
  }
  100%{
    opacity: 1;
  }
}
